body {
  min-width: 320px;
  max-width: 1600px;
  margin: 0 auto;
  overflow-x: hidden;
  font-size: 16px;
  line-height: 30px;
  font-family: "Lato", "Arial", sans-serif;
  color: #848789;
  font-weight: normal;
  font-style: normal;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  body {
    font-size: 14px;
    line-height: 24px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}

.container {
  width: 1100px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container {
    width: auto;
    padding: 0 40px;
  }
}
@media (max-width: 1023px) {
  .container {
    width: auto;
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .container {
    width: auto;
    padding: 0 20px;
  }
}

.btn {
  height: 50px;
  padding: 0px 20px;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 2.8px;
  text-align: center;
  line-height: 50px;
  color: #fff;
  border: solid 1px rgba(255, 255, 255, 0.5019607843);
  cursor: pointer;
  text-decoration: none;
  background-color: transparent;
  outline: none;
  transition: all 0.2s ease-out;
}
.btn:hover, .btn:focus, .btn:active {
  background-color: #fff;
  color: black;
}

.title {
  position: relative;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 35px;
  font-weight: bold;
  letter-spacing: 3.6px;
  text-align: center;
  color: #353738;
}
.title::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .title {
    font-size: 16px;
    line-height: 24px;
  }
}

.toggle {
  width: 40px;
  height: 24px;
}
.toggle::before, .toggle::after, .toggle span {
  content: "";
  position: absolute;
  left: 0;
  width: 40px;
  height: 2px;
  background-color: #4d4959;
  transition: all 0.2s ease-out 0s;
}
.toggle span {
  top: 50%;
  margin-top: -1px;
  opacity: 1;
}
.toggle::before {
  top: 0;
}
.toggle::after {
  bottom: 0;
}
.main-nav--opened .toggle {
  width: 20px;
  height: 21px;
}
.main-nav--active .toggle span {
  opacity: 0;
}
.main-nav--active .toggle::before, .main-nav--active .toggle::after {
  bottom: auto;
  top: 50%;
  margin-top: -1px;
}
.main-nav--active .toggle::before {
  transform: rotate(45deg);
}
.main-nav--active .toggle::after {
  transform: rotate(-45deg);
}

.page-header {
  position: relative;
  height: 800px;
  max-height: 100vh;
  background: #eff0f8 url(../img/header/hero-header.jpg) center top/cover no-repeat;
  z-index: 999;
}
@media (max-width: 1199px) {
  .page-header {
    height: 700px;
  }
}
@media (max-width: 1023px) {
  .page-header {
    height: 600px;
  }
}
@media (max-width: 767px) {
  .page-header {
    height: 75px;
    background-image: radial-gradient(#f5f6fc, #d9d4dc);
  }
  .page-header--fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    transform: translateY(0%);
  }
  .page-header--hide {
    transform: translateY(-100%);
  }
}

.main-nav {
  position: relative;
}
@media (max-width: 767px) {
  .main-nav {
    height: 100%;
  }
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: block;
  }
}

.main-nav__logo {
  position: absolute;
  top: 35px;
  left: 50%;
  transform: translate(-50%, 0);
}
@media (max-width: 767px) {
  .main-nav__logo {
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 500px) {
  .main-nav__logo {
    left: 20px;
    transform: translate(0, -50%);
  }
}

.main-nav__list-wrapper {
  display: flex;
  padding-top: 35px;
}
@media (max-width: 767px) {
  .main-nav__list-wrapper {
    left: 0;
    top: 100%;
    height: calc(100vh - 100%);
    width: 100%;
    background-color: #fff;
    display: block;
    position: absolute;
    padding-top: 15px;
    overflow: auto;
    transform: translateX(-100%);
    transition: all 0.1s ease-out 0s;
  }
  .main-nav--active .main-nav__list-wrapper {
    transform: translateX(0);
  }
}

.main-nav__col {
  width: 50%;
  display: flex;
}
.main-nav__col--1 {
  padding-right: 110px;
  justify-content: flex-end;
}
.main-nav__col--2 {
  padding-left: 110px;
}
@media (max-width: 1023px) {
  .main-nav__col--1 {
    padding-right: 80px;
  }
  .main-nav__col--2 {
    padding-left: 80px;
  }
}
@media (max-width: 767px) {
  .main-nav__col {
    width: auto;
    padding: 0;
    display: block;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .main-nav__list {
    display: block;
  }
}

.main-nav__item:not(:last-child) {
  margin-right: 30px;
}
.main-nav__item:not(:last-child)::after {
  content: ".";
  padding-left: 30px;
}
.main-nav__list--1 .main-nav__item:first-child {
  margin-left: auto;
}
@media (max-width: 767px) {
  .main-nav__item {
    margin: 0;
  }
  .main-nav__item::after {
    display: none;
  }
}

.main-nav__link {
  font-size: 13px;
  line-height: 85px;
  font-weight: bold;
  letter-spacing: 2.6px;
  color: #4d4959;
  text-transform: uppercase;
  white-space: nowrap;
  transition: color 0.2s ease-out;
  cursor: default;
}
.main-nav__link:not(.active):hover {
  color: #68c4a2;
  cursor: pointer;
}
@media (max-width: 767px) {
  .main-nav__link {
    padding: 0 20px;
    line-height: 55px;
  }
}

.logo__link {
  display: block;
}

.logo__img {
