.main-nav {
  position: relative;
  @media (max-width: $md3) {
    height: 100%;
  }
}

.main-nav__toggle {
  display: none;

  @media (max-width: $md3) {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: block;
  }
}

.main-nav__logo {
  position: absolute;
  top: 35px;
  left: 50%;
  transform: translate(-50%, 0);

  @media (max-width: $md3) {
    top: 50%;
    transform: translate(-50%, -50%);
  }

  @media (max-width: 500px) {
    left: 20px;
    transform: translate(0, -50%);
  }
}

.main-nav__list-wrapper {
  display: flex;
  padding-top: 35px;

  @media (max-width: $md3) {
    left: 0;
    top: 100%;
    height: calc(100vh - 100%);
    width: 100%;

    background-color: #fff;
    display: block;
    position: absolute;
    padding-top: 15px;
    overflow: auto;
    transform: translateX(-100%);
    transition: all 0.1s ease-out 0s;

    .main-nav--active & {
      transform: translateX(0);
    }
  }
}

.main-nav__col {
  width: 50%;
  display: flex;
  &--1 {
    padding-right: 110px;
    justify-content: flex-end;
  }

  &--2 {
    padding-left: 110px;
  }

  @media (max-width: $md2) {
    &--1 {
      padding-right: 80px;
    }

    &--2 {
      padding-left: 80px;
    }
  }

  @media (max-width: $md3) {
    width: auto;
    padding: 0;
    display: block;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $md3) {
    display: block;
  }
}

.main-nav__item {
  &:not(:last-child) {
    margin-right: 30px;
  }

  &:not(:last-child)::after {
    content: '.';
    padding-left: 30px;
  }



  .main-nav__list--1 &:first-child {
    margin-left: auto;
  }

  @media (max-width: $md3) {
    margin: 0;
    &::after {
      display: none;
    }
  }
}


.main-nav__link {
  font-size: 13px;
  line-height: 85px;
  font-weight: bold;
  letter-spacing: 2.6px;
  color: #4d4959;
  text-transform: uppercase;
  white-space: nowrap;
  transition: color 0.2s ease-out;
  cursor: default;
  &:not(.active):hover {
    color: #68c4a2;
    cursor: pointer;
  }

  @media (max-width: $md3) {
    padding: 0 20px;
    line-height: 55px;
  }
}
