.btn  {
  height: 50px;
  padding: 0px 20px;
  font-family: $font-stack-default;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 2.8px;
  text-align: center;
  line-height: 50px;
  color: #fff;
  border: solid 1px #ffffff80;
  cursor: pointer;
  text-decoration: none;
  background-color: transparent;
  outline: none;
  transition: all 0.2s ease-out;

  &:hover, &:focus, &:active {
    background-color: #fff;
    color: black;
  }
}
