.reviews {
  padding-top: 124px;
  padding-bottom: 68px;
  background-color: #f7f7f7;

  @media (max-width: $md1) {
    padding-top: 60px;
    padding-bottom: 50px;
  }

  @media (max-width: $md2) {
    padding-top: 30px;
    padding-bottom: 20px;
  }
}
.reviews__list {
  margin-bottom: 100px;

  @media (max-width: $md2) {
    margin-bottom: 70px;
  }
}
.reviews__item { }
.reviews__header {
  position: relative;
  padding-top: 65px;
  margin-bottom: 25px;
  text-align: center;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 32px;
    height: 27px;
    background:  url(../../img/comments/icon-twitter.png) top center / cover no-repeat;
  }

  @media (max-width: $md2) {
    padding-top: 55px;
    margin-bottom: 15px;
  }
}

.reviews__title {
  display: inline;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1.8px;
  color: #353738;

  @media (max-width: $md2) {
    font-size: 16px;
  }
}

.reviews__time {
  font-weight: 300;
  color: #848789;
}
.reviews__text {
  font-size: 24px;
  font-style: italic;
  line-height: 34px;
  text-align: center;
  color: #848789;

  @media (max-width: $md1) {
    width: 600px;
    margin: 0 auto;
    font-size: 20px;
    line-height: 30px;
  }

  @media (max-width: $md3) {
    width: auto;
    font-size: 16px;
    line-height: 24px;
  }
}
.reviews__pagination { }

.swiper-pagination-bullet {
  width: 11px;
  height: 11px;
  margin: 0 9px;
  border: solid 1px #c8c8c8;
  background-color: transparent;
  opacity: 1;
  &-active {
    background-color: #282f356c;
  }
}
