body {
  min-width: 320px;
  max-width: 1600px;
  margin: 0 auto;
  overflow-x: hidden;

  font-size: 16px;
  line-height: 30px;
  font-family: $font-stack-default;
  color: $font-color;
  font-weight: normal;
  font-style: normal;

  background-color: $bg-color;

  @media (max-width: $md3) {
    font-size: 14px;
    line-height: 24px;
  }
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}
