.features { }
.features__list {

}
.features__item {
}
.feature {
  display: flex;

  @media (max-width: $md3) {
    flex-direction: column;
  }
}
.feature__side-img-container {
  position: relative;
  flex: 1 0 0;

  @media (max-width: $md3) {
    width: 100%;

    &--last {
      display: none;
    }
  }
}
.feature__side-img {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  object-fit: cover;

  @media (max-width: $md3) {
    display: block;
    position: static;
    height: auto;
    width: 100%;
    margin: 0 auto;
    object-fit: initial;
  }
}
.feature__main {
  width: 33.33333333%;

  @media (max-width: $md1) {
    width: 40%;
  }

  @media (max-width: $md2) {
    width: 50%;
  }

  @media (max-width: $md3) {
    order: -1;
    width: auto;
  }
}
.feature__content {
  padding: 54px 67px;

  @media (max-width: $md1) {
    padding: 50px 55px;
  }

  @media (max-width: $md2) {
    padding: 30px 30px;
  }
}
.feature__title {
  padding-top: 55px;
  margin-bottom: 15px;

  &--1::before {
    width: 41px;
    height: 30px;
    background-image: url('../img/features/icon2.png');
  }

  &--2::before {
    width: 32px;
    height: 32px;
    background-image: url('../img/features/icon3.png');
  }

  &--3::before {
    width: 23px;
    height: 41px;
    background-image: url('../img/features/icon4.png');
  }

  @media (max-width: $md3) {
    padding-top: 40px;

    &::before {
      transform-origin: top center;
      transform: translateX(-50%) scale(0.7);
    }

  }
}
.feature__text {
  position: relative;
  padding-bottom: 35px;

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    width: 50px;
    height: 1px;
    background-color: #35373865;
  }

  @media (max-width: $md2) {
    padding-bottom: 20px;
  }

  @media (max-width: $md3) {
    padding-bottom: 20px;

    &::before {
      display: none;
    }
  }
}
.feature__main-img-container {
  height: 200px;

  @media (max-width: $md1) {
    height: 150px;
  }

  @media (max-width: $md2) {
    height: 120px;
  }

  @media (max-width: $md3) {
    height: auto;
  }
}
.feature__main-img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;

  @media (max-width: $md3) {
    margin: 0 auto;
    width: auto;
    height: auto;
    object-fit: initial;
  }
}
