.new-gen {
  padding-top: 117px;
  padding-bottom: 140px;
  background-color: #f7f7f7;

  @media (max-width: $md1) {
    padding: 70px 0;
  }

  @media (max-width: $md3) {
    padding: 30px 0;
  }
}

.new-gen__title {
  padding-top: 70px;
  margin-bottom: 30px;

  &::before {
  width: 52px;
  height: 43px;
  background-image: url('../../img/features/icon1.png');
  }

  @media (max-width: $md3) {
    padding-top: 50px;
    margin-bottom: 15px;

    &::before {
      width: 52px * 0.7;
      height: 43px * 0.7;
      }
  }
}

.new-gen__text {
  font-size: 18px;
  line-height: 34px;
  text-align: center;
  color: #848789;

  @media (max-width: $md3) {
    font-size: 14px;
    line-height: 24px;
  }
}
