.page-footer {
  height: 250px;
  background-color: #f7f7f7;

  & .container {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: space-between;
  }

  @media (max-width: $md2) {
    height: 200px;
  }

  @media (max-width: $md3) {
    height: auto;
    padding-top: 30px;
    padding-bottom: 30px;

    & .container {
      height: auto;
      flex-direction: column;
    }
  }
}
.adress {
  font-size: 14px;
  font-style: italic;
  line-height: 18px;
  text-align: left;
  color: #848789;
  @media (max-width: $md3) {
    margin-bottom: 20px;
  }
}
.adress__city {
  font-size: 16px;
  font-weight: bold;
  font-style: normal;
}
.social {
  @media (max-width: $md3) {
    margin-bottom: 20px;
  }
}
.social__list {
  width: 135px;
  margin-right: 20px;
  display: flex;
  justify-content: space-between;
}
.social__item {
  width: 35px;
  height: 35px;
  border-radius: 50%;

  &:nth-child(1) {
    background-color: #79cef0;
  }

  &:nth-child(2) {
    background-color: #527cbe;
  }

  &:nth-child(3) {
    background-color: #ec5654;
  }
}
.social__link {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.social__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &--twitter {
    width: 20px;
    height: 20px;
  }

  &--facebook {
    width: 20px;
    height: 20px;
  }

  &--pinterest {
    width: 20px;
    height: 20px;
  }
}
.copyright {
  display: flex;
  align-items: center;
}
.copytight__text {
  margin-right: 9px;
  font-size: 14px;
  font-style: italic;
  line-height: 18px;
  text-align: right;
  color: #848789;
}
.copyright__logo { }
