.toggle {
  width: 40px;
  height: 24px;

  &::before, &::after, & span {
    content: '';
    position: absolute;
    left: 0;
    width: 40px;
    height: 2px;

    background-color: #4d4959;
    transition: all 0.2s ease-out 0s;
  }

  & span {
    top: 50%;
    margin-top: -1px;
    opacity: 1;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  .main-nav--opened & {
    width: 20px;
    height: 21px;
  }

  .main-nav--active & span {
    opacity: 0;
  }

  .main-nav--active &::before,  .main-nav--active &::after{
    bottom: auto;
    top: 50%;
    margin-top: -1px;
  }

  .main-nav--active &::before {
    transform: rotate(45deg);
  }

  .main-nav--active &::after {
    transform: rotate(-45deg);
  }
}


