.gallery {
  @media (max-width: 960px) {
    width: 320px;
    margin: 0 auto;
  }
}
.gallery__images {
  display: flex;
  height: 534px;

  @media (max-width: 960px) {
    height: 300px;
  }
}
.gallery__col {
  flex: 1 0 0;
  display: flex;
  flex-wrap: wrap;

  @media (max-width: 960px) {
    flex: 0 0 auto;
  }
}
.gallery__img-container {
  width: 100%;
  height: 100%;

  &.half-width {
    width: 50%;
  }

  &.half-height{
    height: 50%;
  }
}
.gallery__link {
  position: relative;
  display: block;
  height: 100%;

  .gallery__img-container--shop-now &:hover::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;

    background-color: #64c29ec4;
    background-image: url('../../img/gallery/shop-icon.png');
    background-position: center 107px;
    background-size: 51px 30px;
    background-repeat: no-repeat;
  }

  .gallery__img-container--shop-now &:hover::after{
    content: 'Shop now';
    position: absolute;
    top: 140px;
    left: 50%;

    font-size: 14px;
    font-weight: bold;
    letter-spacing: 2.8px;
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    transform: translateX(-50%);;
  }

  @media (max-width: 960px) {
    .gallery__img-container--shop-now &:hover::before,
    .gallery__img-container--shop-now &:hover::after {
      display: none;
    }

  }
}

.gallery__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery__bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  background-color: #282f35;

  @media (max-width: $md2) {
    height: 150px;
  }

  @media (max-width: $md3) {
    height: 100px;
  }
}
.gallery__discover {
  min-width: 300px;
}
