.order {
  padding-top: 109px;
  padding-bottom: 111px;

  @media (max-width: $md2) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @media (max-width: $md3) {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.order__logo {
  width: 94px;
  margin: 0 auto;
  margin-bottom: 35px;

  @media (max-width: $md3) {
    margin-bottom: 20px;
  }
}

.order__slogan {
  margin-bottom: 32px;
  font-size: 24px;
  font-style: italic;
  line-height: 34px;
  text-align: center;
  color: #848789;

  @media (max-width: $md3) {
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 25px;
  }
}

.order__form {
  display: flex;
  justify-content: center;

  @media (max-width: $md3) {
    flex-direction: column;
    align-items: center;
  }
}

.order__input-wrap {
  margin-right: 8px;

  @media (max-width: $md3) {
    margin: 0;
    margin-bottom: 20px;
  }
}

.order__input {
  height: 69px;
  width: 400px;
  padding-left: 28px;
  font-size: 20px;
  font-style: italic;
  line-height: 69px;
  color: #929292;
  outline: none;

  border: solid 1px #d3d7d9;

  &:focus {
    border-color: #85dab9;
  }

  @media (max-width: $md3) {
    width: 300px;
  }
}

.order__input::placeholder {
  color: #92929269;
}

.order__submit {
  height: 69px;
  min-width: 126px;
  padding: 0 20px;
  font-size: 24px;
  font-weight: 300;
  line-height: 69px;
  text-align: center;
  color: #fff;
  background-color: #64c29e;

  &:hover, &:active, &:focus {
    background-color: #42ac85;
  }
}
