.page-header {
  position: relative;
  height: 800px;
  max-height: 100vh;
  background: #eff0f8 url(../img/header/hero-header.jpg) center top / cover no-repeat;
  z-index: 999;

  @media (max-width: $md1) {
    height: 700px;
  }

  @media (max-width: $md2) {
    height: 600px;
  }

  @media (max-width: $md3) {
    height: 75px;
    background-image: radial-gradient(#f5f6fc, #d9d4dc);

    &--fixed {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      transform: translateY(0%);
    }

    &--hide {
      transform: translateY(-100%);
    }
  }
}
