.title {
  position: relative;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 35px;
  font-weight: bold;
  letter-spacing: 3.6px;
  text-align: center;
  color: #353738;

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;
  }

  @media (max-width: $md3) {
    font-size: 16px;
    line-height: 24px;
  }
}
